                }
                for(let i=0; i<langName.length;i++){
                    langName[i].style.color='#C1C1C1'
                }

            }
        }
    },
    mounted() {
        this.moduleSwipeNext()
        this.moduleSwipePrev()
        this.checkTheme()
    }
};
</script> 